// 	justify-content: space-between;
// }

.form__politic input {
	display: none;
}

.form__politic {
	width: 261px;
	padding-top: 5px;
	padding-left: 32px;

	@media (max-width: $tablet-width) {
		width: 272px;
	}

	@media (max-width: $mobile-width) {
		padding-top: 27px;
		padding-left: 35px;
	}
}

.form__agreement {
	position: relative;

	color: #fff;
	font-size: 14px;
	font-weight: 400;
	line-height: 16px;

	opacity: 0.8;
	cursor: pointer;

	&::before {
		content: "";

		position: absolute;
		top: 2px;
		left: -32px;

		width: 14px;
		height: 14px;

		border: 1px solid #fff;
	}

	&::after {
		content: "";

		position: absolute;
		top: 5px;
		left: -27px;
		
		display: none;
		width: 4px;
		height: 6px;

		border-right: 1px solid #fff;
		border-bottom: 1px solid #fff;
		transform: rotate(45deg);
	}
}

.form__politic input:checked+ .form__agreement::after {
	display: block;
}