.popup {
	position: fixed;
	z-index: 999999999;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	background-color:rgba(0,0,0,.8);
}

.popup__container {
	position: fixed;
	z-index: 999;
	top: 136px;
	left: 50%;

	width: 490px;
	margin-left: -245px;
	padding-top: 31px;
	padding-left: 39px;
	padding-right: 39px;
	padding-bottom: 36px;

	background-color: #00B2FF;
}

.popup__title {
	margin: 0;
	margin-bottom: 11px;

	font-family: 'RobotoCondesed', Arial, sans-serif;
	color: #fff;
	font-size: 28px;
	font-weight: 400;
	line-height: 140.62%;
}

.popup__text {
	margin: 0;
	margin-bottom: 30px;
	padding-right: 20px;

	color: #fff;
	font-size: 16px;
	font-weight: 400;
	line-height: 22px;
}

.popup input,
.popup textarea {
	padding-left: 27px;

	color: #fff;
	font-size: 16px;
	font-weight: 400;
	line-height: 22px;
}

.popup textarea {
	padding-top: 13px;
	letter-spacing: 1px;
}

.popup__button {
	font-family: 'RobotoCondesed', Arial, sans-serif;
	color: #fff;
	font-size: 14px;
	font-weight: 700;
	line-height: 16px;
	text-transform: uppercase;
	letter-spacing: 0.05em;
}

.popup__agreement {
	color: #fff;
	font-size: 14px;
	font-weight: 400;
	line-height: 16px;

	opacity: 0.8;
}

.popup__close {
	position: absolute;
	top: 20px;
	right: 20px;

	width: 16px;
	height: 16px;

	background-color: transparent;
	background-image: url("../img/close-popup.png");
	background-repeat: no-repeat;
	background-position: center;
	border: none;
	cursor: pointer;
}

.popup form {
	display: block;
	padding-left: 0;
}

.popup input {
	height: 52px;

	width: 100%;
	margin-bottom: 30px;

	background-color: #19baff;
	border: none;
}

.popup textarea {
	width: 100%;
	min-width: 290px;
	height: 104px;
	margin-top: 0;
	margin-bottom: 27px;

	color: #fff;

	background-color: #19baff;
	border: none;
}

::placeholder {
  color: #fff;
}

.popup__politic input {
	display: none;
}

.popup__politic {
	padding-top: 33px;
	padding-left: 51px;
}

.popup__agreement {
	position: relative;

	color: #fff;
	font-size: 14px;
	font-weight: 400;
	line-height: 16px;

	opacity: 0.8;
	cursor: pointer;

	&::before {
		content: "";

		position: absolute;
		top: 2px;
		left: -49px;

		width: 14px;
		height: 14px;

		border: 1px solid #fff;
	}

	&::after {
		content: "";

		position: absolute;
		top: 5px;
		left: -43px;
		
		display: none;
		width: 4px;
		height: 6px;

		border-right: 1px solid #fff;
		border-bottom: 1px solid #fff;
		transform: rotate(45deg);
	}
}

.popup__politic input:checked+ .popup__agreement::after {
	display: block;
}

.popup__button {
	width: 100%;
	display: inline-block;
    padding: 14px 0;
    border: 1px solid #fff;
    border-radius: 5px;

	font-family: 'RobotoCondesed', Arial, sans-serif;
	color: #fff;
	font-size: 14px;
	font-weight: 700;
	line-height: 16px;
	text-transform: uppercase;
	letter-spacing: 0.05em;
	text-align: center;
	vertical-align: middle;

	background-color: transparent;
	cursor: pointer;
}
